<template>
  <div style="padding:20px;">
    <Form ref="sales" :model="sales" :rules="rules" label-position="left" :label-width="120">
      <FormItem label="活动类型" prop="itemmodel">
        <Select v-model="sales.itemmodel"   class="formItem" :disabled="type">
          <Option  :value='4' >套餐商品</Option>
          <Option  :value='5' >精选推荐</Option>
        </Select>
      </FormItem>
      <FormItem label="活动商品" v-if="type">
        <Input v-model="itemName" disabled class="formItem"></Input>
      </FormItem>
      <FormItem label="活动商品" prop="itemid" v-else>
        <item-select v-model='sales.itemid' @on-selected='handleSelect' disabled></item-select>
      </FormItem>
      <!-- <FormItem label="活动数量" prop="num" >
        <InputNumber v-model='sales.num'  placeholder='请输入活动数量' :min="0" class="formItem" :precision="0"></InputNumber>
      </FormItem> -->
      <FormItem label="原始库存">
        <Input v-model='originalNum' disabled class="formItem"></Input>
      </FormItem>
      <FormItem>
        <!-- <Button type='primary' @click="handleSave('sales')">
          {{type?'保存编辑':'保存创建'}}
        </Button> -->
        <Button type='primary' @click="handleSave('sales')" v-if="!type">
          保存创建
        </Button>
        <Button @click="cancel">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  export default {
    data() {
      const validateId=(r,v,callback)=>{
        if(v===''){
          callback(new Error('请选择商品'));
        }{
          callback();
        }
      };
      const validateNum=(r,v,callback)=>{
        if(v===''||v===null){
          callback(new Error('请输入数量'));
        }else if(v>this.originalNum){
          callback(new Error('活动数量不能大于库存'));
        }else{
          callback();
        }
      };
      return {
        type:!!this.$route.query.type,
        sales: {
          itemmodel: 4,
          itemid: '',
          // num: 0,
          price: 0,
          ishidden:0,
        },
        originalNum:0,
        itemName:'',
        rules: {
          itemid:[
            { validator: validateId, trigger: 'blur' }
          ],
          // num: [
          //   { validator: validateNum, trigger: 'blur' }
          // ],
        },
      };
    },
    mounted() {
      this.$sitemap.prvname = "促销商品";
      this.$sitemap.currname =this.type?'编辑': '新建';
      if(this.type){
        this.getItem();
      }
    },
    methods: {
      //获取item信息
      getItem(){
        let that=this;
        let data={ itemid: this.$route.query.id };
        that.$http.post(this.$api.item_detail,data).then(res => {
          let resData = res.data.data;
          if(res.data.result=='1'&&resData) {
            that.setItem(resData.item);
          }else{
            that.$Message.error(res.data.msg);
          }
        }).catch(err => {
          that.$Message.error('服务器异常');
        });
      },
      //表单赋值
      setItem(item) {
        this.sales.itemmodel=item.itemmodel;
        this.sales.price = item.price;
        // this.sales.num = item.num;
        this.sales.itemid=item.id;
        this.originalNum = item.num;
        this.itemName=item.title;
      },
      handleSelect(item) {
        console.log(item);
        // this.sales.num = item.num;
        this.sales.price = item.price;
        this.originalNum = item.num;
      },
      //格式化数据
      formatData(){
        let params={};
        let that=this;
        Object.keys(that.sales).forEach(key => {
          switch (key){
            default:
              params[key] = that.sales[key];
              break;
          }
        });
        return params;
      },
      //创建提交
      handleSave(name) {
        let that=this;
        that.$refs[name].validate((valid) => {
          if(valid){
            this.$http.post(this.$api.item_editmodel, that.formatData()).then(res => {
              if(res.data.result==1){
                that.$Message.success('提交成功');
                that.$router.push('/salesitem/list');
              }else{
                that.$Message.error(res.data.msg);
              }
            }).catch(err => {
              that.$Message.error('服务器异常');
            })
          }else{
            that.$Message.error('请核对信息');
          }
        });
      },
      cancel(){
        this.$router.push('/salesitem/list');
      }
    },
  }
</script>
<style>
  .formItem{
    width: 350px;
  }
</style>



